import './scss/Ticker.scss'
const Ticker = ({ CustomTicker }) => {
    const { title, ticker_position, tickerType, ticker_height, ticker_speed, subTitle } = CustomTicker;

    const tickerSpeed = [5, 10].includes(ticker_speed) ? ticker_speed : 20;
    return  <div className={`ti_container ti_pos-${ticker_position} ti_type-${tickerType}`}>
                
                {tickerType != 2 ? <div className={`ti_title_wrap`}>
                    <div className={`ti_title`}>{title}</div>
                </div>:null}
                <div className='ti_m_wrap'>
                    <marquee className='ti_marquee' scrollamount={tickerSpeed}>
                        <div className={`ti_desc`}>
                            {subTitle}
                        </div>
                    </marquee>
                </div>
            </div>
}

export default Ticker;  